:root {
  --text-color-hover: #00acdc;
  --text-color-basic: #000;
}

body {
  color: var(--text-color-basic);
  height: 60px;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

.header {
  justify-content: space-between;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  margin-top: 10px;
}

.logotype {
  width: 40px;
  height: 40px;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.item {
  margin-left: 20px;
  position: relative;
}

.item:first-child {
  margin-left: 0;
}

.link {
  text-transform: uppercase;
  color: var(--text-color-basic);
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.link:hover {
  color: var(--text-color-hover);
}

.navigation {
  height: 60px;
  margin-top: 0;
  margin-bottom: 0;
}

.is-active {
  color: var(--text-color-hover);
}

.is-active:after {
  content: "";
  background-color: var(--text-color-hover);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
